<template>
  <div class="activeDetails">
    <div class="container">
      <div class="activeTitle">
        <span class="back" @click="goBack"></span>
        <i class="el-icon-arrow-right"></i> 
        <span></span>
      </div>
      <!--  -->
      <div class="requirement">
        <div class="require-left">
           <span>
            
            <el-cascader
              :options="sortOption"
              v-model="sortValue"
              @change="selectType"
              placeholder=""
              @expand-change="selectType"
            >
            </el-cascader>
          </span>
          <span>
            
            <el-select :clearable="clearable" v-model="cityDefault" placeholder="" class="selected" @change="selectCity">
              <el-option   
              v-for="item in cityOptions"
              :key="item"
              :value="item">
              </el-option>
            </el-select>
          </span>
          <span>
            
            <el-select v-model="sourceValue" placeholder="" class="selected" @change="selectFrom">
            <el-option
            v-for="item in sourceOptions"
            :key="item.value2"
            :label="item.label2"
            :value="item.value2">
            </el-option>
            </el-select>
          </span>
        </div>
        <div class="require-right">
          <span class="search">
            <i class="el-icon-search"></i>
            <el-input v-model.trim="search" placeholder="" class="inputSearch"></el-input>
            <el-button type="primary" class="searchButton" @click="inputSearch"></el-button>
          </span>
          <span class="clear" @click="clear">
            
          </span>
        </div>
      </div>
      <div class="activeContent">
        <div class="activesTitle clearfix">
         <span>
            {{titleName}}
         </span>
         <span class="exportExcel" v-if="optionArr[0]">
           <a href="javascript:;" @click="ExportExcel"><i class="el-icon-right"></i></a>
         </span>
         <span class="activesEnter">
           <a href="javascript:;" @click="EnterActivePage">
              <i class="el-icon-right"></i>
           </a>
         </span>
        </div>
        <div class="searchResult">
          <el-table :data="optionArr" stripe style="width: 100%" id="table" :header-cell-style="{background:'#d8d8d8',color:'#606266','font-weight':400}">
            <el-table-column type="index" :index="indexMethod" label="" width='100'></el-table-column>
            <el-table-column prop="blogTitle" width='250' label=""></el-table-column>
            <el-table-column prop="blogOrder" label=""></el-table-column>
            <el-table-column prop="tagTypeName2" label=""></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                {{scope.row.msgFrom | getmsgFrom}}
              </template>
            </el-table-column>
            <el-table-column prop="userNickname" label="" ></el-table-column>
            <el-table-column prop="district" label=""></el-table-column>
            <el-table-column prop="score" label="" ></el-table-column>
          </el-table>
        </div>
      </div>
      <div class="page">
        <el-pagination
          background
          layout="prev, pager, next"
          :total="total"
          @current-change="handleCurrentChange"
          >
        </el-pagination>
       </div>
    </div>
  </div>
</template>
<style lang="less" scoped>
.activeDetails{
  text-align: left;
  .container{
    margin:60px 0 60px 260px;
    .activeTitle{
      color: #a2a2a2;
      height: 58px;
      font-size: 14px;
      line-height: 58px;
      padding-left: 50px;
      border-bottom: 1px solid #eee;
      .back{
        color: #5a5a5a;
        cursor: pointer;
      }
    }
    .requirement{
      text-align: left;
      font-size: 12px;
      padding:20px 50px;
      overflow: hidden;
      .require-left{
        float: left;
      }
      .require-right{
        float: right;
      }
      span{
        margin-right: 20px;
        display: inline-block;
        margin-bottom: 20px;
        .selected{
          margin-left: 8px;
          width: 120px;
        }
        .el-cascader{
          width: 120px;
        }
      }
      .clear {
        display: inline-block;
        width: 60px;
        height: 38px;
        border: 1px solid #409EFF;
        color: #409EFF;
        text-align: center;
        line-height: 38px;
        border-radius: 3px;
        font-size: 14px;
        cursor: pointer;
      }
      .search{
        .el-icon-search{
          position: relative;
          left: 26px;
          z-index: 2;
        }
        .inputSearch{
          width: auto;
          left: 3px;
        }
        .searchButton{
          font-size: 14px;
        }
      }
    }
    .activeContent{
      .activesTitle{
        padding:0 50px 20px 50px;
        span:first-child{
          font-size: 16px;
          font-weight: 700;
          color: #707070;
          float: left;
        }
        .activesEnter,.exportExcel{
          background-color: #ffffff;
          border: 1px solid #4cb4e7;
          width: 120px;
          height: 24px;
          line-height: 24px;
          color: #4cb4e7;
          float: right;
          margin-right: 50px;
          text-align: center;
          cursor: pointer;
          a{
            color: #4cb4e7;
          }
        }
      }
      .searchResult{
        padding: 0px 50px;
        text-align: center;
        .noData{
          padding : 1.5rem;
        }
        .tableTr{
          margin:0;
          display: flex;
          background: rgb(216,216,216);
          li{
            list-style-type: none;
            flex: 1;
            height: 50px;
            line-height: 50px;
            font-size: 14px;
            width: 120px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            .draft{
              color: #8f8fff;
            }
            .noExamine{
              color: rgb(7,210,210);
            }
            .passed{
              color: rgb(81,223,66);
            }
            .reject{
              color: rgb(217,72,52);
            }
          }
        }
        .list{
          .tableTr{  				
            li{
              font-size: 12px;
              position: relative;
            }
          }
          .tableTr:nth-child(2n+1){
            background: rgb(241,246,254);
          }
          .tableTr:nth-child(2n){
            background: white;
          }
        }
      }
    }
    .page{
      margin-top: 50px;
      text-align: center;
    }
  }
 }
</style>
<script>
import subContent from '@/components/subContent'
import XLSX from 'xlsx'
import FileSaver from 'file-saver'
const sortOption = [{
        value: [],
        label: '',
        children:[{
          value: [],
          label: ''
        }]
      }]
const sourceOptions = [{
        value2: -1,
        label2: ''
      },{
        value2: 1,
        label2: ''
      },{
        value2: 2,
        label2: ''
      },{
        value2: 3,
        label2: ''
      }]
export default {
  name:'activeDetails',
  data(){
    return{
      titleName:this.$route.query.title,
      optionArr:[],
      cityDefault:'',//
      cityOptions:this.districtList,//
      sortOption:sortOption,//
      sortValue:"",
      value: "",
      sourceOptions: sourceOptions,//
      sourceValue: '',//
      search: '',
      total: 0,
      blogId: "",
      pageNum: 1,
      clearable: true,
      EnterActiveUrl:this.api.configIp + "/xinghuo-apaas-wkj/index.html#/Event?activityId=" + this.$route.query.activityId
    }
  },
  created(){
    this.getCategory()
    this.getAuditBlog()
  },
  filters:{
    getmsgFrom(item){
      switch(item){
        case 1:
          return ''
          break;
        case 2:
          return ''
          break;
        case 3:
          return ''
          break
      }
    },
  },
  methods:{
    EnterActivePage(){
      window.open(this.EnterActiveUrl)
    },
    ExportExcel() {
      let table =  document.getElementById('table');
      let workbook = XLSX.utils.table_to_book(document.getElementById('table'))
      try {
        XLSX.writeFile(workbook, this.titleName +'' + '.xlsx');
      } catch(e) {
        console.log(e, workbook);
      }
	  },
    indexMethod(index) {
      if(this.pageNum==1){
        return index + 1
      }else{
        return (this.pageNum-1)*10  +index+1;
      }
    },
    goBack(){
      this.$router.back(-1)
    },
    //
    selectType(val) {
      console.log(val)
      this.sortValue = val;
      this.getAuditBlog()
    },
    selectFrom(val) {
      this.sourceValue = val;
      this.getAuditBlog()
    },
    selectCity(val) {
      this.cityDefault = val;
      this.getAuditBlog()
    },
    inputSearch() {
      if(this.search==""){
        this.$message.warning('!')
        return
      }
      this.getAuditBlog();
    },
    //
    getCategory() {
      this.$post(`${this.url}/manage/category/getAllCategory`,
        {}
      )
      .then((response) => {
        if(response.code == 200){
          let data = response.data;
          
          const newTeam = function (data, parentId) {
            let itemArr = [];
            for (let i = 0; i < data.length; i++) {
              let node = data[i];

              if (node.parentCategoryId == parentId) {
                let newNode = {};
                newNode.value = node.categoryId;
                newNode.label = node.categoryName;
                itemArr.push(newNode);
                if(newTeam(data, node.categoryId).length != 0){
                  newNode.children = newTeam(data, node.categoryId);     
                }            
              }
            }
            return itemArr;              
          }
          
          this.sortOption = [...this.sortOption, ...newTeam(data, 0)];

        }else{
          this.$message.error('');
        }
      })
    },
    getAuditBlog() {
        let url = this.url + this.net.queryBlogRatingPager;
        let tagTypeId = this.sortValue;
        let msgFrom = this.sourceValue;
        let blogTitle = this.search;
        let district = this.cityDefault;

        if(!tagTypeId){
          tagTypeId = ["", ""];
        }

        if(tagTypeId.join("") == ""){
          tagTypeId = ["",""];
        }
        if(typeof(msgFrom) == "string" || msgFrom == -1){
          msgFrom = "";
        }
        if(district == ''){
          district = "";
        }
        
        let params = {
          activityId:this.$route.query.activityId,
          district:district,
          pageNum  : this.pageNum,
          pageSize : 10,
          tagTypeId1:tagTypeId[0],
          tagTypeId2:tagTypeId[1],
          msgFrom:msgFrom,
          searchKey: this.search,
          orderByParam:"{score:-1}"
        };

        this.$post(url,params).then((res) => {
          if(res.code == 200){
            this.optionArr = res.data.result;
            this.total = res.data.total;
            this.pageNum = res.data.pageNum;
          }else{
            this.$message.error('');
          }
        }).catch((err)=>{
            console.log('err:',err)
        })
    },
    clear() {
      this.search = '';
      this.sortValue = '';
      this.sourceValue = '';
      this.cityDefault=''
      this.getAuditBlog();
    },
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getAuditBlog()
    },
  },
  components:{
    subContent,
  }
}
</script>


